body {
  margin: 0;
  font-family: 'Be Vietnam Pro', -apple-system, BlinkMacSystemFont, Segoe UI,
  Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue,
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* my styling */
html {
  background-color: black;
  font-family: 'Inter';
  color: white;
}

/* title */
.title {
  text-align: center;
  font-weight: bolder;
  padding: 0em;
  font-size: 0.8em;
}

.title img {
  width: 2.5em;
  margin-left: 0px;
}

/* image of gif */
.constructiongif {
  border-radius: 15px;
  width: 90%;
  
  /* center */
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.beachgif {
  border-radius: 15px;
  width: 80%;
  
  /* center */
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.marinagif {
  border-radius: 15px;
  width: 90%;
  
  /* center */
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.traderjoesgif {
  border-radius: 15px;
  width: 90%;
  
  /* center */
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* description of gif styling */
.gifDescription {
  color: rgb(182, 179, 179);
  text-align: center;
  font-size: 0.8em;
  padding: 0.8em;

}

/* h3 styling */
.main h3 {
  padding-top: 0.1em;
  padding-bottom: 0.1em;
  padding-left: 1em;
  margin: 0;
}

/* card styling */
.card {
  margin: 1.2em;
  padding: 0.9em;
  background-color: black;
  color: rgb(182, 179, 179);
  border-radius: 15px;
  border: 1px solid rgb(182, 179, 179);
}

.card h4 {
  color: white;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

.cardContainer {
  margin-bottom: 4em;
}

/* iframe styling */
.video-container {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
}

.video-container video {
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  height: 90%;
  border-radius: 15px;
  border: 1px solid rgb(182, 179, 179);
  margin-left: 5%;
}

/* media queries */
@media screen and (min-width: 500px) {
  .main {
    padding: 0% 25%;
  }
}