.navbar {
    display: flex;
    margin: 0.1em;

    /*locks navbar on the bottom*/
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    text-align: center;
}

.navbar button {
    flex: 1;
    border: 0px;
    margin: 0.7em;
    padding: 0.5em;
    font-size: 60%;
    border: 1px solid rgb(182, 179, 179);
}

button {
    color: white;
    background-color: black;
    border-radius: 5px;
    font-size: 0.7em;
    font-weight: bold;
}

/* media queriese */
@media screen and (min-width: 500px) {
    .navbar {
      padding: 0% 25%;
    }
  }